function generateOutput(input: string, output: string) {
  // output 0 .outputinput
  // output01
  // .return
  if ('1234567890.'.indexOf(input) >= 0) {
    if (output.indexOf('.') >= 0 && input === '.') return output;
    if (output === '0' && input !== '.') {
      output = input;
    } else {
      output += input;
    }
    return output;
  }
  if (input === '') {
    return output.slice(0, -1) || '0';
  }
  if (input === '') {
    return '0';
  }
  return '0';
}

export {generateOutput};